.sro {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

@font-face {
  font-family: "Geomanist";
  src: url("../fonts/Geomanist-Medium.otf") format("opentype");
}
@font-face {
  font-family: "Source sans pro";
  src: url("../fonts/SourceSansPro-Bold.ttf") format("truetype");
  font-weight: 700;
}
@font-face {
  font-family: "Source sans pro";
  src: url("../fonts/SourceSansPro-Regular.ttf") format("truetype");
  font-weight: 400;
}
@font-face {
  font-family: "Source sans pro";
  src: url("../fonts/SourceSansPro-SemiBold.ttf") format("truetype");
  font-weight: 600;
}
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img,
svg,
video,
canvas,
audio,
iframe,
embed,
object {
  display: block;
  vertical-align: middle;
}

* {
  box-sizing: border-box;
}

button,
a {
  cursor: pointer;
}

.sro {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

a {
  cursor: pointer;
}

body {
  overflow-x: hidden;
}

.btn {
  border-radius: 5px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #6442fb;
  color: #f7f4ff;
  border: 2px solid #6442fb;
  transition: cubic-bezier(0.6, -0.28, 0.735, 0.045) 0.25s;
  min-height: 44px;
  width: 100%;
  max-width: calc(1em * 20);
  margin: 0 auto;
}
.btn:hover {
  color: white;
}
.btn--secondary {
  background-color: white;
  border: 2px solid #6442fb;
  color: #6442fb;
}
.btn--secondary:hover {
  background-color: #f7f4ff;
  color: #6442fb;
}
.btn--fw {
  grid-column: 1/3;
  justify-self: center;
  max-width: 266px;
  width: calc(100% - 1em);
}

.header {
  max-width: 100%;
  min-height: 3.8125em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #6442fb;
  position: fixed;
  width: 100%;
}
.header--normal {
  position: initial;
}
.header__title {
  order: 0;
  text-decoration: none;
  font-size: 1.7925em;
  font-family: Geomanist, "Trebuchet MS", Helvetica, sans-serif;
  font-weight: 600;
  color: white;
  text-align: center;
}
.header__title__right {
  margin-right: 0.625em;
}
.header__title--fw {
  width: 100%;
  margin-bottom: 1.0460251046em;
}
.header__menu--teacher, .header__menu--student, .header__menu--app {
  font-family: "Source sans pro", Tahoma, Verdana, sans-serif;
  color: white;
  background: #3d38ff radial-gradient(circle, #3d38ff 0%, #6443ff 100%);
  position: absolute;
  top: 0;
  width: 201px;
  height: 100vh;
  transition: 0.3s ease-in-out;
  z-index: 2;
}
.header__menu--teacher, .header__menu--student {
  left: -201px;
  padding: 14px 0 0;
}
.header__menu--app {
  left: 100vw;
  display: flex;
  flex-direction: column;
  padding: 14px 10px 23px;
}
.header #main__menu:checked ~ nav {
  transform: translateX(-201px);
}
.header #c__menu:checked ~ nav {
  transform: translateX(201px);
}

.menu__link {
  display: flex;
  align-items: center;
  max-width: 190px;
  min-height: 40px;
  margin-bottom: 1.215em;
  letter-spacing: 0.8px;
  line-height: 1.2857142857em;
  text-decoration: none;
  color: #dbdbff;
  transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.3s;
}
.menu__link:hover {
  color: white;
}
.menu__link__icon {
  margin: 0 10px 0 16px;
  width: 23px;
  flex-shrink: 0;
}

#currentpage {
  color: white;
  border-left: 2px solid white;
}
#currentpage .menu__link__icon {
  margin: 0 10px 0 14px;
  stroke: #fff;
}

.menu__link__icon {
  stroke: #ccb4ed;
}

.menu__title,
.amenu__title {
  font-size: 1.125em;
  font-weight: 600;
  max-width: 140px;
  margin-bottom: 1.22222222em;
}

.amenu__user {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.amenu__img {
  margin-right: 10px;
  border-radius: 50%;
}
.amenu__first, .amenu__name {
  color: white;
  text-decoration: underline white;
  line-height: 1.4em;
  display: block;
}
.amenu__form {
  margin-top: auto;
}
.amenu__btn {
  margin: 0 auto;
  background-color: transparent;
  border: 1px solid white;
  font-weight: 600;
  color: white;
  padding: 1.2857142857em 0;
  text-align: center;
  width: 160px;
}

.menu__title {
  margin-left: auto;
}
.menu__label--right, .menu__label--left {
  cursor: pointer;
  position: relative;
  z-index: 100;
}
.menu__label--right {
  order: 1;
  margin-right: 0.625em;
}
.menu__label--left {
  order: -1;
  margin-left: 0.625em;
}
.menu__label {
  width: 30px;
}
.menu__icon {
  width: 22px;
  height: 22px;
  color: #d09579;
}
.menu__icon--right {
  right: 0;
}
.menu__icon--left {
  left: 0;
}

.content,
.content__container {
