.table {
  margin-top: 1em;
  border-radius: 5px 5px 0 0;
  background-color: #fff;
  width: 100%;
}
.table--borders {
  border-radius: 5px;
}
.table--nested {
  margin: 0 auto 1em;
  width: calc(100% - 0.5em);
  border-radius: 5px;
}

.header__cell,
.body__cell {
  display: table-cell;
  color: #252525;
  border-bottom: 1px solid #f7f4ff;
}

.header__cell {
  font-weight: 600;
  text-align: left;
  border-bottom: 1px solid #6442fb;
}
.header__link {
