}

.end {
  background-color: white;
  border-radius: 0 0 5px 5px;
}
.end__cta {
  background-color: white;
  padding: 1.25em 0.625em 0.6em;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 0 0 5px 5px;
}
.end__cta--two {
  display: flex;
  justify-content: space-between;
}
.end__cta--vertical {
  display: block;
}
.end__content {
  color: #252525;
  font-size: 1.125em;
  font-weight: 600;
  margin: 0 0 1.1111111111em;
}
.end__link, .end__btn--half {
  font-size: 0.875em;
  padding: 0.7142857143em;
}
.end__btn--half {
  width: calc(50% - 7px);
}
.end__link {
  color: white;
}
.end--small {
