}
.login__title {
  display: block;
  max-width: 240px;
  margin: 0 auto;
}

#logo {
  width: 50px;
  height: 50px;
  display: block;
  margin: 30px auto 20px;
}

.form--mw {
  max-width: 300px;
  margin: 0 auto;
}
.form--pd {
  padding-bottom: 2.316em;
}
.form--half {
  width: calc(50% - 7px);
}
.form__add__book {
  border-radius: 5px 5px 0 0;
  background-color: white;
  margin: 0.833em 0 0;
  padding: 1em 0.5em 0.675em;
