}

.more__info {
  font-weight: 600;
  margin-bottom: 1em;
}
.more__container {
  padding: 2.316em 0 1.389em;
  color: #3e3e3e;
}
.more__inline {
  margin-bottom: 0.6em;
}
.more__content {
  font-weight: 600;
}
.more__info, .more__content {
