  padding: 0.833em;
  border-radius: 5px;
  margin: 0.675em 0 1em;
  background-color: #f7f4ff;
}

tr:last-child .body__cell {
  border: none;
}

html {
  max-width: 800px;
  margin: 0 auto;
}

@media (min-width: 450px) {
  .order__content {
    display: flex;
    align-items: center;
  }
  .order__status__link {
    margin: 1em;
  }

  .content__infos {
    font-size: 1em;
  }
}
@media (min-width: 620px) {
  .body__cell {
    font-size: 1em;
  }

  .end__link {
    font-size: 1em;
  }

  .book__info {
    font-size: 1em;
  }

  .btn {
    font-size: 1em;
  }
}
@media (min-width: 800px) {
  .header {
    max-width: 800px;
  }
  .header #c__menu:checked ~ nav {
    transform: translateX(401px);
  }
  .header #main__menu:checked ~ nav {
    transform: translateX(-401px);
  }

  .header__menu--teacher,
.header__menu--student {
    left: -401px;
  }

  .header__menu--app {
    left: 1000px;
  }
}
@media (min-width: 1202px) {
  #app_title {
    margin: 0 auto;
  }

  .menu__title {
    margin-left: 10px;
  }

  .menu__label--left, .menu__label--right,
.menu__label #c__menu,
.menu__label #main__menu {
    display: none;
  }

  .header__menu--teacher,
.header__menu--student {
    left: -201px;
  }

  .header__menu--app {
    left: 800px;
  }
}